@charset "UTF-8";
/* Обнуление стилей */
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, * :before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-style: 100%;
  line-height: 1;
  font-style: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-style: inherit;
  font-weight: 400;
}

body {
  background-color: var(--main-color);
}

.sidebars {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}

.profile {
  z-index: 2;
  position: relative;
}

.chat,
.messages,
.friends {
  position: relative;
  z-index: 2;
}

.message-search,
.friend-search,
.user-category-selection-panel,
.chat-header {
  position: fixed;
  z-index: 2;
}

html {
  font-size: var(--html-fz);
}

body {
  font-family: var(--ff);
}

#root {
  height: 100vh;
  width: 100%;
}

.wrapper {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

