.userCategeoryButton {
   flex: 0 0 33.3333%;
   height: 100%;
   text-align: center;
   border-right: 1px solid var(--border-color);

   transition: ease 0.1s opacity;
}

.notActive {
   color: #959595;
   font-weight: 400;
}

.active {
   color: var(--theme-color);
   font-weight: 500;
}

.userCategeoryButton :hover {
   background-color: rgba(235, 235, 235, 0.55);
}

.button {
   background-color: inherit;

   font-size: var(--main-fz);

   display: block;

   width: 100%;
   height: 100%;

   line-height: 1.5rem;

   padding: 1rem 0;

   transition: ease 0.1s opacity;
}

.activeLink {
   font-weight: 500;
   color: var(--theme-color);
}
