.friendsItem {
   display: grid;
   grid-template-columns: 8rem 1fr 10rem;
   grid-auto-rows: 2.7rem 2.7rem 2.7rem;
   grid-template-areas:
      "avatar name      addButton"
      "avatar online    addButton"
      "avatar education addButton";
   align-items: center;

   padding: 1.2rem 2rem;

   background-color: var(--message-color);
}

.friendsItem:hover {
   cursor: pointer;
   background-color: rgba(235, 235, 235, 0.55);
}

.offline,
.online,
.name,
.education {
   padding-left: 2.5rem;
}

.name {
   grid-area: name;

   font-size: var(--main-fz);
   color: var(--base-fontColor);
   font-weight: 500;
}

.education {
   grid-area: education;

   font-size: var(--main-fz);
   line-height: 1.5rem;
   color: var(--base-fontColor);
   opacity: 0.56;
}

.offline,
.online {
   grid-area: online;
   justify-self: start;

   font-size: var(--secondary-fz);
   font-weight: 400;
   line-height: 1.3rem;
}

.offline {
   color: var(--base-fontColor);
   opacity: 0.56;
}

.online {
   color: var(--theme-color);
   font-weight: 500;
}

.addButton:hover {
   background-color: var(--theme-hover);
   filter: drop-shadow(0px 2px 5px var(--theme-hover-shadow));
}

.addButton {
   grid-area: addButton;
   justify-self: end;
   align-self: center;

   width: fit-content;
   padding: 0.6rem 1.7rem;
   border-radius: 0.5rem;
   font-weight: 500;
   font-size: var(--main-fz);
   filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25));
   transition-property: filter background-color;
   transition-duration: 0.1s;
   background-color: var(--theme-color);
   color: var(--main-color);
}