.gridContainer {
   display: grid;
   grid-template-columns: minmax(5rem, max-content) minmax(10rem, max-content);
   grid-template-rows: repeat(2, minmax(2.5rem, max-content));
   grid-template-areas: "avatar name"
                        "avatar onOffline";

   padding: 0.75rem 0;

   align-items: center;
}

.gridContainer:hover {
   cursor: pointer;
}
