}

.name,
.offline,
.online {
   padding-left: 1rem;
}

.name {
   grid-area: name;
   font-size: var(--main-fz);
   color: var(--base-fontColor);
   font-weight: 500;
}

.offline,
.online {
   grid-area: onOffline;

   font-size: var(--secondary-fz);
   font-weight: 400;
}

.offline {
   color: var(--base-fontColor);
   opacity: 0.56;
}

.online {
   color: var(--theme-color);
   font-weight: 500;
}